import React, { Component } from 'react'
import { Tabs, Table } from 'antd';
import { reqSolo } from '../../api/index.js'
import { connect } from 'react-redux';
import { actionCreators } from './store';

const { TabPane } = Tabs;
const JIDU = ['', '', '', '']
//
const columns = [
    {
        title: '',
        dataIndex: 'department',
        key: 'department',
        render: text => <strong style={{ width: 100, display: 'block' }}>{text}</strong>,
    },
    { title: '', dataIndex: 'names', key: 'names' }
]

class SoloQuantization extends Component {
    state = {
        soloList: [],//
    }
    //
    tabClick = async (key) => {
        let result;
        result = await reqSolo(key)
        const { success, content } = result
        if (success) {
            this.setState({ soloList: content })
        } else {
            console.log('222')
        }
    }
    render() {
        console.log('qianduan', this.props.list.content)
        return (
            <div>
                <Tabs
                    type="card"
                    centered
                    onTabClick={this.tabClick}
                    defaultActiveKey=''>
                    {
                        JIDU.map((item) => (
                            <TabPane tab={item} key={item}>
                                {
                                    item === '' ?
                                        <Table
                                            showHeader={false}
                                            pagination={false}
                                            bordered={true}
                                            rowKey={record => record._id}
                                            dataSource={this.props.list.content}
                                            columns={columns}
                                        >
                                        </Table>
                                        :
                                        <Table
                                            pagination={false}
                                            bordered={true}
                                            rowKey={record => record._id}
                                            dataSource={this.state.soloList} columns={columns}
                                        >
                                        </Table>
                                }

                            </TabPane>
                        ))
                    }
                </Tabs>
            </div>
        )
    }
    componentDidMount() {
        this.props.get_solo();
    }
}
const mapStateToProps = (state) => ({
    list: state.getIn(['solo', 'data'])
})
const mapDispatch = (dispatch) => ({
    get_solo() {
        const action = actionCreators.get_solo_detail();
        dispatch(action);
    }
});
export default connect(mapStateToProps, mapDispatch)(SoloQuantization);