import { Col, Row, Form, Input, Button, Select, message } from 'antd'
import React, { Component, } from 'react'
import DocumentTitle from 'react-document-title'
import { LEADER_LIST } from '../../consant/Consant'
import Footer from '../footer/Footer'
import Header from '../header/Header'
// import { BASE_ALL_DEPARTMENT } from '../../consant/Consant'//
import { reqAddMail } from '../../api'

const { Item } = Form
const { TextArea } = Input
const { Option } = Select
//
const layout = { labelCol: { span: 6 }, wrapperCol: { span: 16 }, };
const tailLayout = { wrapperCol: { offset: 6, span: 16 }, };


class MailDetail extends Component {
    //
    onFinish = async values => {
        console.log('Success:', values);
        const result = await reqAddMail(values)
        if (result.err === 0) {
            message.success('')
            this.props.history.push('/success', values.leader)
        }
        else {
            message.error('')
        }

    }
    //
    onFinishFailed = errorInfo => { console.log('Failed:', errorInfo); };

    render() {
        //
        console.log(this.props.match.params.attr)
        //title
        let toptitle;
        let i;
        for (i = 0; i <= LEADER_LIST.length; i++) {
            if (LEADER_LIST[i].abbr === this.props.match.params.attr) {
                toptitle = LEADER_LIST[i].leader
                break;
            }
        }
        // const attr = this.props.match.params.attr
        return (
            <DocumentTitle title={`${toptitle}`}>
                <>
                    <Header />
                    <Row>
                        <Col span={16} offset={4} style={{ marginTop: 60 }}>
                            <Form {...layout} name="basic"
                                initialValues={{ leader: this.props.match.params.attr }}
                                onFinish={this.onFinish}
                                onFinishFailed={this.onFinishFailed}>
                                <Item label='' name='leader' rules={[{ required: true, message: '' }]}>
                                    <Select >
                                        <Option value="zw"></Option>
                                        <Option value="zz"></Option>
                                        <Option value="zr"></Option>
                                        <Option value="jw"></Option>
                                    </Select>
                                </Item>

                                <Item label="" name="title" rules={[{ required: true, message: '' }]} >
                                    <Input placeholder='' />
                                </Item>
                                <Item label="" name="content" rules={[{ required: true, message: '' }]} >
                                    <TextArea rows={6} placeholder='' />
                                </Item>
                                <Item label="" name="department"
                                    rules={[{ message: '' },]}>
                                    <Input placeholder='' />
                                </Item>
                                <Item label="" name="name" rules={[{ message: '' }]}>
                                    <Input placeholder='' />
                                </Item>
                                <Form.Item {...tailLayout}> <Button block type="primary" htmlType="submit"></Button> </Form.Item>
                            </Form>
                        </Col>
                    </Row>
                    <Footer />
                </>
            </DocumentTitle>
        )
    }
}
export default MailDetail;